'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { motion, AnimatePresence } from 'framer-motion';
import {
  Award,
  Trophy,
  Star,
  Zap,
  Lock,
  ChevronLeft,
  Sparkles,
  Target,
  Medal,
  Crown,
} from 'lucide-react';
import { ProtectedRoute } from '@/components/auth/ProtectedRoute';
import { Card } from '@/components/ui/Card';
import {
  getMyBadges,
  getBadgeProgress,
  getBadgeLeaderboard,
  getBadgeCategories,
  type UserBadge,
  type BadgeProgress,
  type LeaderboardEntry,
} from '@/lib/api/badges';

const rarityColors: Record<string, string> = {
  common: 'from-gray-400 to-gray-500',
  uncommon: 'from-green-400 to-green-600',
  rare: 'from-blue-400 to-blue-600',
  epic: 'from-purple-400 to-purple-600',
  legendary: 'from-amber-400 to-orange-500',
};

const rarityBorders: Record<string, string> = {
  common: 'border-gray-300 dark:border-gray-600',
  uncommon: 'border-green-400',
  rare: 'border-blue-400',
  epic: 'border-purple-400',
  legendary: 'border-amber-400',
};

export default function BadgesPage() {
  const [activeTab, setActiveTab] = useState<'my-badges' | 'progress' | 'leaderboard'>('my-badges');
  const [myBadges, setMyBadges] = useState<UserBadge[]>([]);
  const [progress, setProgress] = useState<BadgeProgress[]>([]);
  const [leaderboard, setLeaderboard] = useState<LeaderboardEntry[]>([]);
  const [categories, setCategories] = useState<string[]>([]);
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    try {
      setLoading(true);
      const [badgesData, progressData, leaderboardData, categoriesData] = await Promise.all([
        getMyBadges(),
        getBadgeProgress(),
        getBadgeLeaderboard(10),
        getBadgeCategories(),
      ]);
      setMyBadges(badgesData);
      setProgress(progressData);
      setLeaderboard(leaderboardData);
      setCategories(['all', ...categoriesData]);
    } catch (error) {
      console.error('Error loading badges:', error);
    } finally {
      setLoading(false);
    }
  };

  const filteredProgress = selectedCategory === 'all'
    ? progress
    : progress.filter(p => p.badge.category === selectedCategory);

  const tabs = [
    { id: 'my-badges', label: 'My Badges', icon: Award, count: myBadges.length },
    { id: 'progress', label: 'Progress', icon: Target },
    { id: 'leaderboard', label: 'Leaderboard', icon: Trophy },
  ];

  return (
    <ProtectedRoute>
      <div className="min-h-screen bg-gray-50 dark:bg-neutral-950 pb-24">
        {/* Header */}
        <div className="bg-gradient-to-r from-amber-500 to-orange-500 dark:from-amber-600 dark:to-orange-600">
          <div className="max-w-4xl mx-auto px-4 py-6">
            <div className="flex items-center gap-3 mb-4">
              <Link
                href="/more"
                className="p-2 bg-white/20 rounded-full hover:bg-white/30 transition-colors"
              >
                <ChevronLeft className="w-5 h-5 text-white" />
              </Link>
              <div>
                <h1 className="text-2xl font-bold text-white flex items-center gap-2">
                  <Award className="w-7 h-7" />
                  Achievements
                </h1>
                <p className="text-amber-100 text-sm">
                  Collect badges and earn XP for your accomplishments
                </p>
              </div>
            </div>

            {/* Tabs */}
            <div className="flex gap-2">
              {tabs.map(tab => (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id as typeof activeTab)}
                  className={`flex items-center gap-2 px-4 py-2 rounded-full font-medium transition-all ${
                    activeTab === tab.id
                      ? 'bg-white text-amber-600'
                      : 'bg-white/20 text-white hover:bg-white/30'
                  }`}
                >
                  <tab.icon className="w-4 h-4" />
                  {tab.label}
                  {tab.count !== undefined && (
                    <span className={`px-2 py-0.5 rounded-full text-xs ${
                      activeTab === tab.id ? 'bg-amber-100' : 'bg-white/20'
                    }`}>
                      {tab.count}
                    </span>
                  )}
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* Content */}
        <div className="max-w-4xl mx-auto px-4 py-6">
          {loading ? (
            <div className="flex items-center justify-center py-20">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-amber-500"></div>
            </div>
          ) : (
            <AnimatePresence mode="wait">
              {activeTab === 'my-badges' && (
                <motion.div
                  key="my-badges"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                >
                  {myBadges.length === 0 ? (
                    <Card className="text-center py-12">
                      <Sparkles className="w-16 h-16 mx-auto text-amber-300 mb-4" />
                      <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">
                        No badges yet!
                      </h3>
                      <p className="text-gray-500 dark:text-gray-400">
                        Complete activities to earn your first badge
                      </p>
                    </Card>
                  ) : (
                    <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                      {myBadges.map((ub, index) => (
                        <motion.div
                          key={ub.id}
                          initial={{ opacity: 0, scale: 0.8 }}
                          animate={{ opacity: 1, scale: 1 }}
                          transition={{ delay: index * 0.05 }}
                        >
                          <Card className={`text-center p-4 border-2 ${rarityBorders[ub.badge.rarity]}`}>
                            <div className={`w-16 h-16 mx-auto mb-3 rounded-full bg-gradient-to-br ${rarityColors[ub.badge.rarity]} flex items-center justify-center text-3xl`}>
                              {ub.badge.icon}
                            </div>
                            <h4 className="font-semibold text-gray-900 dark:text-white text-sm">
                              {ub.badge.name}
                            </h4>
                            <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                              {ub.badge.description}
                            </p>
                            <div className="mt-2 flex items-center justify-center gap-1 text-amber-500">
                              <Zap className="w-3 h-3" />
                              <span className="text-xs font-medium">+{ub.badge.xpReward} XP</span>
                            </div>
                          </Card>
                        </motion.div>
                      ))}
                    </div>
                  )}
                </motion.div>
              )}

              {activeTab === 'progress' && (
                <motion.div
                  key="progress"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  className="space-y-4"
                >
                  {/* Category Filter */}
                  <div className="flex gap-2 overflow-x-auto pb-2">
                    {categories.map(cat => (
                      <button
                        key={cat}
                        onClick={() => setSelectedCategory(cat)}
                        className={`px-4 py-2 rounded-full text-sm font-medium whitespace-nowrap transition-colors ${
                          selectedCategory === cat
                            ? 'bg-amber-500 text-white'
                            : 'bg-white dark:bg-neutral-800 text-gray-700 dark:text-gray-300 hover:bg-amber-100 dark:hover:bg-neutral-700'
                        }`}
                      >
                        {cat === 'all' ? 'All Categories' : cat}
                      </button>
                    ))}
                  </div>

                  {/* Progress Cards */}
                  {filteredProgress.map((bp, index) => (
                    <motion.div
                      key={bp.badge.id}
                      initial={{ opacity: 0, x: -20 }}
                      animate={{ opacity: 1, x: 0 }}
                      transition={{ delay: index * 0.03 }}
                    >
                      <Card className={`p-4 ${bp.unlocked ? 'border-2 ' + rarityBorders[bp.badge.rarity] : ''}`}>
                        <div className="flex items-center gap-4">
                          <div className={`w-14 h-14 rounded-full flex items-center justify-center text-2xl ${
                            bp.unlocked
                              ? `bg-gradient-to-br ${rarityColors[bp.badge.rarity]}`
                              : 'bg-gray-200 dark:bg-neutral-700'
                          }`}>
                            {bp.unlocked ? bp.badge.icon : <Lock className="w-6 h-6 text-gray-400" />}
                          </div>
                          <div className="flex-1">
                            <div className="flex items-center gap-2">
                              <h4 className="font-semibold text-gray-900 dark:text-white">
                                {bp.badge.name}
                              </h4>
                              {bp.unlocked && (
                                <span className="text-xs px-2 py-0.5 bg-green-100 dark:bg-green-900/30 text-green-600 dark:text-green-400 rounded-full">
                                  Unlocked!
                                </span>
                              )}
                            </div>
                            <p className="text-sm text-gray-500 dark:text-gray-400">
                              {bp.badge.description}
                            </p>
                            {!bp.unlocked && (
                              <div className="mt-2">
                                <div className="flex items-center justify-between text-xs mb-1">
                                  <span className="text-gray-500">
                                    {bp.progress.current} / {bp.progress.target}
                                  </span>
                                  <span className="text-amber-500 font-medium">
                                    {bp.progress.percentage}%
                                  </span>
                                </div>
                                <div className="h-2 bg-gray-200 dark:bg-neutral-700 rounded-full overflow-hidden">
                                  <div
                                    className="h-full bg-gradient-to-r from-amber-400 to-orange-500 rounded-full transition-all"
                                    style={{ width: `${bp.progress.percentage}%` }}
                                  />
                                </div>
                              </div>
                            )}
                          </div>
                          <div className="text-right">
                            <div className="flex items-center gap-1 text-amber-500">
                              <Zap className="w-4 h-4" />
                              <span className="font-semibold">{bp.badge.xpReward}</span>
                            </div>
                            <span className={`text-xs capitalize ${
                              bp.badge.rarity === 'legendary' ? 'text-amber-500' :
                              bp.badge.rarity === 'epic' ? 'text-purple-500' :
                              bp.badge.rarity === 'rare' ? 'text-blue-500' :
                              bp.badge.rarity === 'uncommon' ? 'text-green-500' :
                              'text-gray-500'
                            }`}>
                              {bp.badge.rarity}
                            </span>
                          </div>
                        </div>
                      </Card>
                    </motion.div>
                  ))}
                </motion.div>
              )}

              {activeTab === 'leaderboard' && (
                <motion.div
                  key="leaderboard"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                >
                  <Card className="overflow-hidden">
                    {/* Top 3 */}
                    <div className="bg-gradient-to-r from-amber-500 to-orange-500 p-6">
                      <div className="flex items-end justify-center gap-4">
                        {leaderboard.slice(0, 3).map((entry, index) => {
                          const positions = [1, 0, 2]; // 2nd, 1st, 3rd
                          const actualIndex = positions[index];
                          const actualEntry = leaderboard[actualIndex];
                          if (!actualEntry) return null;
                          
                          const heights = ['h-20', 'h-28', 'h-16'];
                          const icons = [Medal, Crown, Medal];
                          const Icon = icons[index];
                          
                          return (
                            <div key={actualEntry.user.id} className="text-center">
                              <div className="relative">
                                <div className={`w-16 h-16 mx-auto rounded-full border-4 border-white overflow-hidden ${
                                  actualIndex === 0 ? 'ring-4 ring-amber-300' : ''
                                }`}>
                                  {actualEntry.user.profileImage ? (
                                    <img
                                      src={actualEntry.user.profileImage}
                                      alt={actualEntry.user.name}
                                      className="w-full h-full object-cover"
                                    />
                                  ) : (
                                    <div className="w-full h-full bg-white/20 flex items-center justify-center text-white font-bold text-xl">
                                      {actualEntry.user.name?.charAt(0)}
                                    </div>
                                  )}
                                </div>
                                <div className={`absolute -bottom-2 left-1/2 -translate-x-1/2 w-6 h-6 rounded-full flex items-center justify-center text-xs font-bold ${
                                  actualIndex === 0 ? 'bg-amber-300 text-amber-900' :
                                  actualIndex === 1 ? 'bg-gray-300 text-gray-700' :
                                  'bg-orange-300 text-orange-900'
                                }`}>
                                  {actualIndex + 1}
                                </div>
                              </div>
                              <p className="text-white font-medium mt-3 text-sm">
                                {actualEntry.user.name}
                              </p>
                              <p className="text-amber-100 text-xs">
                                {actualEntry.badgeCount} badges
                              </p>
                            </div>
                          );
                        })}
                      </div>
                    </div>

                    {/* Rest of leaderboard */}
                    <div className="divide-y divide-gray-100 dark:divide-neutral-800">
                      {leaderboard.slice(3).map(entry => (
                        <div
                          key={entry.user.id}
                          className="flex items-center gap-4 p-4 hover:bg-gray-50 dark:hover:bg-neutral-800/50"
                        >
                          <div className="w-8 h-8 rounded-full bg-gray-100 dark:bg-neutral-700 flex items-center justify-center text-sm font-bold text-gray-600 dark:text-gray-300">
                            {entry.rank}
                          </div>
                          <div className="w-10 h-10 rounded-full overflow-hidden bg-gray-200 dark:bg-neutral-700">
                            {entry.user.profileImage ? (
                              <img
                                src={entry.user.profileImage}
                                alt={entry.user.name}
                                className="w-full h-full object-cover"
                              />
                            ) : (
                              <div className="w-full h-full flex items-center justify-center text-gray-500 font-semibold">
                                {entry.user.name?.charAt(0)}
                              </div>
                            )}
                          </div>
                          <div className="flex-1">
                            <p className="font-medium text-gray-900 dark:text-white">
                              {entry.user.name}
                            </p>
                            <p className="text-sm text-gray-500 dark:text-gray-400">
                              @{entry.user.username}
                            </p>
                          </div>
                          <div className="flex items-center gap-1 text-amber-500">
                            <Award className="w-4 h-4" />
                            <span className="font-semibold">{entry.badgeCount}</span>
                          </div>
                        </div>
                      ))}
                    </div>
                  </Card>
                </motion.div>
              )}
            </AnimatePresence>
          )}
        </div>
      </div>
    </ProtectedRoute>
  );
}
